@media (min-width: 768px) {
	.sidenav-collapsed .sidenav-heading {
		display: none;
	}
	.sidenav-collapsed .img-sidemenu{
		display: block;
	}
	.sidenav-collapsed .sidenav-icon {
		float: none;
		font-size: 18px;
		height: 16px;
		left: 0;
		line-height: 20px;
		margin-left: 20px;
		margin-right: 0;
		position: absolute;
		top: 15px;
		-webkit-transition: font-size 0.3s;
		-o-transition: font-size 0.3s;
		transition: font-size 0.3s;
		width: 16px;
		display:block
	}

	.sidenav-collapsed .sidenav-label {
		display: none;
		content: "\f00b";
		padding-left: 11px;
	}

	.sidenav-collapsed .sidenav-form {
		display: none;
	}

	.sidenav-collapsed .sidenav-badge {
		display: block;
		position: absolute;
		right: 5px;
		top: 5px;
	}

	.sidenav-collapsed li.open>a:before {
		-webkit-transform: rotate(0);
		-ms-transform: rotate(0);
		-o-transform: rotate(0);
		transform: rotate(0);
	}

	.sidenav-collapsed li.has-subnav>a:before {
		text-align: right;
	}

	.sidenav-collapsed.level-1>li>a {
		height: 45px;
		line-height: 45px;
		padding: 0;
	}

	.sidenav-collapsed.level-1>li.has-subnav>a:before {
		content: normal;
		display: none;
	}

	.sidenav-collapsed.level-1>li:hover>a:after,
	.sidenav-collapsed.level-1>li.hover>a:after {
		content: normal;
		display: none;
	}

	.sidenav-collapsed .sidenav {
		display: none !important;
		height: auto !important;
		left: 100%;
		min-width: 160px;
		overflow: visible !important;
		position: absolute;
		top: 0;
		z-index: 1030;
	}

	.sidenav-collapsed .sidenav>li>a {
		border-left: 1px solid #e7e7ec;
		padding-left: 15px;
		padding-right: 15px;
		white-space: nowrap;
	}
	
	.sidenav-collapsed .sidenav .sidenav-heading {
		background-color: #fff;
		color: #323232;
		cursor: default;
		display: block;
		font-size: 13px;
		line-height: 45px;
		overflow: hidden;
		padding: 0 15px;
		text-overflow: ellipsis;
		white-space: nowrap;
		margin-top: 0;
	}

	.sidenav-collapsed .sidenav .icon {
		margin-left: 0px;
		margin-right: 5px;
	}

	.sidenav-collapsed .active .sidenav-heading {
		background-color: #fff;
		color: #13b6bc;
	}

	.sidenav-collapsed .has-subnav:hover>.sidenav,
	.sidenav-collapsed .has-subnav.hover>.sidenav {
		display: block !important;
	}
}

@-webkit-keyframes spinner-animation {
	from {
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}

	to {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}

@-o-keyframes spinner-animation {
	from {
		-o-transform: rotate(0deg);
		transform: rotate(0deg);
	}

	to {
		-o-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}

@keyframes spinner-animation {
	from {
		-webkit-transform: rotate(0deg);
		-o-transform: rotate(0deg);
		transform: rotate(0deg);
	}

	to {
		-webkit-transform: rotate(360deg);
		-o-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}

.spinner:before,
.spinner:after {
	border-color: rgba(0, 0, 0, 0.1);
	border-radius: 50%;
	border-style: solid;
	content: "";
	left: 50%;
	position: absolute;
	top: 50%;
	border-width: 2px;
	height: 24px;
	margin: -12px 0 0 -12px;
	width: 24px;
}

.spinner:after {
	-webkit-animation: spinner-animation 0.6s linear;
	-o-animation: spinner-animation 0.6s linear;
	animation: spinner-animation 0.6s linear;
	-webkit-animation-iteration-count: infinite;
	-o-animation-iteration-count: infinite;
	animation-iteration-count: infinite;
}

