.steps {
	list-style: none;
	margin-bottom: 0;
	padding-left: 0;
}

.steps:before,
.steps:after {
	content: " ";
	display: table;
}

.steps:after {
	clear: both;
}

.step {
	color: #7d7d8e;
	display: block;
	padding: 0;
	text-align: center;
}

.step-segment {
	display: block;
	padding-bottom: 5px;
	padding-top: 5px;
	position: relative;
}

.step-segment:after {
	background-color: #13b6bc;
	content: "";
	display: block;
	height: 2px;
	position: absolute;
	right: 50%;
	top: 50%;
	-webkit-transform: translateY(-50%);
	-ms-transform: translateY(-50%);
	-o-transform: translateY(-50%);
	transform: translateY(-50%);
	width: 100%;
}

.step:first-child .step-segment:after {
	display: none;
}

.step-icon {
	background-color: #13b6bc;
	border: 2px solid #13b6bc;
	border-radius: 50%;
	-webkit-box-sizing: content-box;
	box-sizing: content-box;
	color: #fff;
	display: inline-block;
	position: relative;
	text-align: center;
	z-index: 2;
	display: inline-block;
	font-size: 24px;
	height: 48px;
	line-height: 48px;
	text-align: center;
	width: 48px;
}

.step.active .step-icon {
	background-color: #fff;
	color: #13b6bc;
}

.step.active~.step .step-segment:after {
	background-color: #e7e7ec;
}

.step.active~.step .step-icon {
	background-color: #fff;
	border-color: #e7e7ec;
	color: #7d7d8e;
}

.switch {
	cursor: pointer;
	display: inline-block;
	height: 20px;
	margin: 0;
	position: relative;
	-webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;
	vertical-align: middle;
	width: 44px;
}

.switch-input {
	opacity: 0;
	filter: alpha(opacity=0);
	position: absolute;
	z-index: -1;
	left: 0;
}

.switch-input:checked~.switch-thumb {
	left: 24px;
}

.switch-track {
	background-color: #dcdcdc;
	border-radius: 10px;
	height: 100%;
	left: 0;
	position: absolute;
	top: 50%;
	-webkit-transform: translateY(-50%);
	-ms-transform: translateY(-50%);
	-o-transform: translateY(-50%);
	transform: translateY(-50%);
	width: 100%;
}

.switch-thumb {
	background-color: #fff;
	border-color: #dcdcdc;
	border-radius: 50%;
	border-style: solid;
	border-width: 4px;
	height: 20px;
	left: 0;
	position: absolute;
	top: 0;
	-webkit-transition: left 0.15s ease-out;
	-o-transition: left 0.15s ease-out;
	transition: left 0.15s ease-out;
	width: 20px;
}

.switch-default .switch-input:checked~.switch-track {
	background-color: #7d7d8e;
}

.switch-default .switch-input:checked~.switch-thumb {
	border-color: #7d7d8e;
}

.switch-primary .switch-input:checked~.switch-track {
	background-color: #13b6bc;
}

.switch-primary .switch-input:checked~.switch-thumb {
	border-color: #13b6bc;
}

.switch-success .switch-input:checked~.switch-track {
	background-color: #13b6bc;
}

.switch-success .switch-input:checked~.switch-thumb {
	border-color: #13b6bc;
}

.switch-info .switch-input:checked~.switch-track {
	background-color: #5ca0e6;
}

.switch-info .switch-input:checked~.switch-thumb {
	border-color: #5ca0e6;
}

.switch-warning .switch-input:checked~.switch-track {
	background-color: #fdb32b;
}

.switch-warning .switch-input:checked~.switch-thumb {
	border-color: #fdb32b;
}

.switch-danger .switch-input:checked~.switch-track {
	background-color: #fd442b;
}

.switch-danger .switch-input:checked~.switch-thumb {
	border-color: #fd442b;
}

.switch .switch-input:disabled~.switch-track {
	background-color: #e7e7ec;
}

.switch .switch-input:disabled~.switch-thumb {
	border-color: #e7e7ec;
	cursor: not-allowed;
}

